import React, {useState, useEffect} from 'react'
import '../App.css'
import NBA from '../pictures/rsz_nba_mockup - Copy.png'
import FB from '../pictures/FB_Bot_MockUp2.png'
import CS50 from '../pictures/CS50_MockUp.png'
import tinymile from '../pictures/tinymile.png'
import ME380 from '../pictures/ME380_2.png'
import VY from '../pictures/VY.PNG'

import styled from 'styled-components'
import {Heart} from '@styled-icons/boxicons-solid/Heart'


export default function ProjectCard() {
    const [Projects, setProjects] = useState([
        {
            id: 0,
            title: 'NBA Fantasy Research',
            frameworks: 'Flask, React, BeautifulSoup, Pandas',
            image: NBA,
            background: '#C9DAF9',
            fit: 'contain',
            desc: 'Web app that streamlines NBA Fantasy research by simultaneously displaying multiple players advanced NBA statistics and injuries',
            link: 'https://william-chuu.github.io/NBA-Fantasy-Research-App/'
        },
        {
            id: 1,
            title: 'Mental Health Chat Bot',
            frameworks: 'Flask, Webhooks, Facebook Messenger API',
            image: FB,
            background: '#e9fcf4',
            fit: 'contain',
            desc: 'Facebook Messenger bot that assesses mental well-being via the PHQ-9 health questionnaire and provides user-oriented quotes to improve morale, productivity and mental health',
            link: 'https://www.facebook.com/TheMentalHealthBot'
        },
        {
            id: 3,
            title: 'C$50 Finance',
            frameworks: 'Flask, BootStrap, SQLite3',
            image: CS50,
            background: '#FEECE8',
            fit: 'contain',
            desc: 'Web app that allows users to quote, buy and sell mock stocks in real-time. CRUD operations allow user\'s to save and update their stock portfolios',
            link: 'https://finance.cs50.net/login'
        }
    ])

    const [School, setSchool] = useState([
        {
            id: 0,
            title: 'Stewart Platform - 1st Place in Competition',
            frameworks: 'Arduino, iOS, Swift, 3D-Printing, Mechanical Design',
            image: ME380,
            background: '#FFFFCE',
            fit: 'contain',
            link: 'https://youtu.be/HZ_O4oQlDkg',
            desc: 'Developed an IOS and Arduino application to successfully control a 6 degrees of freedom platform and complete a ball maze. Minimized latency between input motion and output orientation to 100ms',
        }
    ])

    const [Work, setWork] = useState([
        {
            id: 1,
            position: 'Software Engineer Intern',
            image: VY,
            link: 'https://www.vidyard.com/',
            desc: ' Worked on the video hosting team, responsible for product strategy surrounding video uploading, editing and playback targeting Enterprise tier customers',
            company: 'Vidyard',
            duration: 'May - Dec \'21',
            fit: 'contain'

        },
        {
            id: 0,
            position: 'Mechanical Engineer Intern',
            image: tinymile,
            link: 'https://tinymile.ai/',
            desc: ' Seed round start-up aiming to bring autonomous delivery robots to the streets of downtown Toronto, working on a 2-person team reporting directly to the CEO',
            company: 'tinymile.ai',
            duration: 'Jan - May \'20',
            background: '#ffc0cb',
            fit: 'contain'

        },
    ])

    const ProjectsContainer = styled.div`
    width: 1000px;
    margin: auto;
    margin-top: 15vh;
    display: flex;
    flex-direction: column;
    `;
    const ProjectsTitle = styled.div`
    font-family: 'Times New Roman', Times, serif;
    font-size: 40px;
    `;

    const ProjectCardContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    margin-top: 100px;
    `;
    const ProjectCard = styled.div`
    width: 800px;
    height: 256px;
    object-fit: cover;
    background-color: white;
    -webkit-transition: margin 0.3s ease-out;
        -moz-transition: margin 0.3s ease-out;
        -o-transition: margin 0.3s ease-out;
    :hover{
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
        margin-bottom: 10px;
        margin-top: -10px;
    }

    `;
    const ProjectPic = styled.img`
    width: 40%;
    height: 256px;
    `;

    const ProjectInfo = styled.div`
    position: relative;
    -moz-box-sizing: border-box; 
    -webkit-box-sizing: border-box; 
    box-sizing: border-box; 
    float:right;
    padding: 25px;
    width: 60%;
    height: 256px;
    `;

    const Title = styled.div`
    font-family:'GoogleSansMedium';
    font-size: 18px;
    `;
    const Desc = styled.div`
    margin-top:20px;
    font-family: 'GoogleSansRegular';
    line-height: 1.7;
    `;
    const Frameworks = styled.div`
    font-size: 15px;
    color: #3A3B3C;
    font-family: 'GoogleSansRegular';
    margin-top: 3px;
    `;
    const Try = styled.a`
    position: absolute;
    bottom: 20px;
    right: 20px;
    font-family:'GoogleSansMedium';
    `;

    const Date = styled.div`
    position: absolute;
    bottom: 20px;
    right: 20px;
    font-family:'GoogleSansMedium';
    `;
    
    const MadeWith = styled.div`
    text-align: center;
    width: 100%;
    margin-top: 200px;
    margin-bottom: 40px;
    `; 
    const heart = styled(Heart)`
    width: 5px;
    `;
    return(
        <React.Fragment>
            <link href="https://emoji-css.afeld.me/emoji.css" rel="stylesheet"></link>
            <ProjectsContainer>
                <ProjectsTitle>
                    For Fun
                </ProjectsTitle>
                <ProjectCardContainer>
                {Projects.map((project) => (
                    // spacing between each card (since title for projects is offset ("For Fun"))
                    <div style={{paddingBottom:'50px', width: '100%'}}>
                        <ProjectCard>
                            <ProjectPic src={project['image']} style={{ background:project['background'], objectFit:project['fit']}}></ProjectPic>
                            <ProjectInfo>
                                <Title>{project['title']}</Title>
                                <Frameworks>{project['frameworks']}</Frameworks>
                                <Desc>{project['desc']}</Desc>
                                <Try href={project['link']} target="_blank">Try Me!</Try>
                            </ProjectInfo>
                        </ProjectCard>
                    </div>
                    ))}
                    <ProjectCard>
                        <ProjectPic src={School[0]['image']} style={{ background:School[0]['background'], objectFit:School[0]['fit']}}></ProjectPic>
                        <ProjectInfo>
                            <Title>{School[0]['title']}</Title>
                            <Frameworks>{School[0]['frameworks']}</Frameworks>
                            <Desc>{School[0]['desc']}</Desc>
                            <Try href={School[0]['link']} target="_blank">Watch</Try>
                        </ProjectInfo>
                    </ProjectCard>

                </ProjectCardContainer>
            </ProjectsContainer>
            
                    {/* WORK */}
            <ProjectsContainer>
                <ProjectsTitle>
                    For Work
                </ProjectsTitle>
                <ProjectCardContainer>
                    {Work.map((work) => (
                    // spacing between each card (since title for projects is offset ("For Fun"))
                    <div style={{paddingBottom:'50px', width: '100%'}}>
                        <ProjectCard>
                            <ProjectPic src={work['image']} style={{ background:work['background'], objectFit:work['fit']}}></ProjectPic>
                            <ProjectInfo>
                                <a href={work['link']}>
                                    <Title >{work['company']}</Title>
                                </a>
                                <Frameworks>{work['position']}</Frameworks>
                                <Desc>{work['desc']}</Desc>
                                <Date>{work['duration']}</Date>
                            </ProjectInfo>
                        </ProjectCard>
                    </div>
                    ))}
                </ProjectCardContainer>
            </ProjectsContainer>

            <MadeWith>
                made with react + <i class="em em-heart" aria-role="presentation" aria-label="HEAVY BLACK HEART"></i>
            </MadeWith>
        </React.Fragment>
    )
}