import React, {useState, useEffect} from 'react'
import '../App.css'

import 'bootstrap/dist/css/bootstrap.min.css';
import Accordion from 'react-bootstrap/Accordion';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import Carousel from 'react-bootstrap/Carousel';

import SF from '../pictures/SF.JPG'
import DEANSLIST from '../pictures/DEANS LIST 2019.jpg'
import volleyball from '../pictures/NACIVT 2019.jpg'

import styled from 'styled-components'

export default function AboutMe() {
    const [Me, setMe] = useState([
        {
            nutshell: 'I build things that live on the internet.',
            nutshell2:'I enjoy solving problems which has led me to learning and developing with a passion every single day.',
            nutshell3: 'I am currently studying Engineering at the Universty of Waterloo and have had 5 previous Engineering internships with track recrods that show strong problem-solving and fast-learning abilites.',
            interests: 'In my spare time I enjoy playing sports, maintaining good personal health and spending time with close friends.',
            interests2:'Currently, I have aspirations to travel more and enjoy what this planet has to offer before life catches up with me.'
        }
    ])

    const AboutContainer = styled.div`
    width: 1000px;
    margin: auto;
    margin-top: 15vh;
    display: flex;
    flex-direction: column;
    `;
    const AboutTitle = styled.div`
    font-family: 'Times New Roman', Times, serif;
    font-size: 40px;
    `;

    const AboutContentContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: auto;
    margin-top: 100px;
    `;    

    return(
        <React.Fragment>
        <link
            rel="stylesheet"
            href="https://maxcdn.bootstrapcdn.com/bootstrap/4.5.0/css/bootstrap.min.css"
            integrity="sha384-9aIt2nRpC12Uk9gS9baDl411NQApFmC26EwAOH8WgZl5MYYxFfc+NcPb1dKGj7Sk"
            crossorigin="anonymous"
        />
            <AboutContainer>
                <AboutTitle>Me</AboutTitle>
                    <AboutContentContainer>
                    <Accordion style={{width: '800px', fontFamily:'GoogleSansRegular'}}>
                        <Card>
                            <Card.Header>
                            <Accordion.Toggle as={Button} variant="link" eventKey="0" style={{color:'black'}}>
                                In a Nutshell
                            </Accordion.Toggle>
                            </Card.Header>
                            <Accordion.Collapse eventKey="0">
                            <Card.Body>{Me[0]['nutshell']} <br></br><br></br> {Me[0]['nutshell2']} <br></br><br></br> {Me[0]['nutshell3']}</Card.Body>
                            </Accordion.Collapse>
                        </Card>
                        <Card>
                            <Card.Header>
                            <Accordion.Toggle as={Button} variant="link" eventKey="1" style={{color:'black'}}>
                                More About Me
                            </Accordion.Toggle>
                            </Card.Header>
                            <Accordion.Collapse eventKey="1">
                            <Card.Body>
                            <Carousel>
                                <Carousel.Item>
                                    <img
                                    className="d-block w-100"
                                    src={SF}
                                    alt="Golden Gate Bridge"
                                    />
                                    <Carousel.Caption style={{backgroundColor: 'rgba(0, 0, 0, 0.5)'}}>
                                    <h3>San Fancisco, California</h3>
                                    <p>Paying tribute to the soon-to-be 2019 champions: Toronto Raptors // 2018</p>
                                    </Carousel.Caption>
                                </Carousel.Item>
                                <Carousel.Item>
                                    <img
                                    className="d-block w-100"
                                    src={volleyball}
                                    alt="Third slide"
                                    style={{height:'568px', objectFit:'cover'}}
                                    />

                                    <Carousel.Caption  style={{backgroundColor: 'rgba(0, 0, 0, 0.5)'}}>
                                    <h3>North American Chinese Invitational Volleyball Tournament Finals</h3>
                                    <p>Photo Credits: @uannasor // Summer 2019</p>
                                    </Carousel.Caption>
                                </Carousel.Item>
                                <Carousel.Item>
                                    <img
                                    className="d-block w-100"
                                    src={DEANSLIST}
                                    alt="Third slide"
                                    style ={{height: '568px', objectFit: 'cover'}}
                                    />

                                    <Carousel.Caption  style={{backgroundColor: 'rgba(0, 0, 0, 0.5)'}}>
                                    <h3>'Dean's List' Stewart Platform Team</h3>
                                    <p>Happiness, hard-work, tiredness and a wide range of heights captured in a photo // Fall 2019</p>
                                    </Carousel.Caption>
                                </Carousel.Item>
                            </Carousel>


                                <br></br><br></br> {Me[0]['interests']} <br></br><br></br> {Me[0]['interests2']}
                            </Card.Body>
                            </Accordion.Collapse>
                        </Card>
                    </Accordion>
                    </AboutContentContainer>
            </AboutContainer>
        </React.Fragment>
    )
}